// src/components/GitHubProjects.js
import React, { useEffect, useState, useRef } from 'react'
import { motion } from 'framer-motion'
import './githubprojects.css'
import Navbar from '../../components/Navbar/Navbar'

export default function GitHubProjects() {
  const [repos, setRepos] = useState([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState(null)
  const [page, setPage] = useState(1)
  const [showScrollTop, setShowScrollTop] = useState(false)

  const sectionRef = useRef(null)

  const username = process.env.REACT_APP_USERNAME
  // This is how many items you want visible on-screen per page
  const pageSize = parseInt(process.env.REACT_APP_PERPAGE || '9', 10)

  useEffect(() => {
    async function fetchRepos() {
      setLoading(true)
      setError(null)
      try {
        if (!username) {
          throw new Error('GitHub username is not configured.')
        }

        // Fetch a big enough set of repos once (100 is the GitHub API max per page)
        const res = await fetch(
          `https://api.github.com/users/${username}/repos?sort=updated&per_page=100`
        )

        if (!res.ok) {
          throw new Error(`GitHub API error: ${res.status}`)
        }

        const data = await res.json()
        setRepos(data)
      } catch (err) {
        setError(err.message)
      } finally {
        setLoading(false)
      }
    }

    fetchRepos()
  }, [username])

  // Show / hide "back to top" button on mobile when scrolling
  useEffect(() => {
    const handleScroll = () => {
      const isMobile = window.innerWidth <= 768
      if (!isMobile) {
        setShowScrollTop(false)
        return
      }

      // Show button after user scrolls a bit down the page
      if (window.scrollY > 200) {
        setShowScrollTop(true)
      } else {
        setShowScrollTop(false)
      }
    }

    window.addEventListener('scroll', handleScroll)
    return () => window.removeEventListener('scroll', handleScroll)
  }, [])

  const scrollToTopOfSection = () => {
    if (sectionRef.current) {
      sectionRef.current.scrollIntoView({
        behavior: 'smooth',
        block: 'start',
      })
    }
  }

  // Pagination math
  const totalPages = Math.max(1, Math.ceil(repos.length / pageSize))
  const currentPage = Math.min(page, totalPages)
  const startIndex = (currentPage - 1) * pageSize
  const endIndex = startIndex + pageSize
  const visibleRepos = repos.slice(startIndex, endIndex)

  const handlePrev = () => {
    setPage((prev) => Math.max(1, prev - 1))
  }

  const handleNext = () => {
    setPage((prev) => Math.min(totalPages, prev + 1))
  }

  const containerVariants = {
    hidden: { opacity: 0, y: 20 },
    show: {
      opacity: 1,
      y: 0,
      transition: {
        staggerChildren: 0.08,
        delayChildren: 0.2,
        type: 'spring',
        stiffness: 100,
      },
    },
  }

  const cardVariants = {
    hidden: { opacity: 0, y: 20, scale: 0.96 },
    show: {
      opacity: 1,
      y: 0,
      scale: 1,
      transition: { type: 'spring', stiffness: 130, damping: 12 },
    },
  }

  return (
    <div className="site-container" ref={sectionRef}>
      <Navbar />
      <motion.section
        ref={sectionRef}
        className="projects-section"
        initial={{ opacity: 0, y: 40 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.6, type: 'spring', stiffness: 120 }}
      >
        <motion.h2
          className="projects-title"
          initial={{ opacity: 0, y: -10 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.1 }}
        >
          GitHub Projects
        </motion.h2>

        {loading && <p className="projects-status">Loading...</p>}
        {error && <p className="projects-error">Error: {error}</p>}
        {!loading && !error && repos.length === 0 && (
          <p className="projects-status">No repositories found.</p>
        )}

        {!loading && !error && visibleRepos.length > 0 && (
          <>
            {/* Pagination controls */}
            <div className="projects-pagination">
              <button
                className="projects-page-button"
                onClick={handlePrev}
                disabled={currentPage === 1 || loading}
              >
                 Prev
              </button>

              <span className="projects-page-indicator">
                Page {currentPage} of {totalPages}
              </span>

              <button
                className="projects-page-button"
                onClick={handleNext}
                disabled={currentPage === totalPages || loading}
              >
                Next 
              </button>
            </div>
            <motion.ul
              className="projects-list"
              variants={containerVariants}
              initial="hidden"
              animate="show"
            >
              {visibleRepos.map((repo) => (
                <motion.li
                  key={repo.id}
                  className="project-card"
                  variants={cardVariants}
                  whileHover={{
                    y: -6,
                    scale: 1.03,
                    boxShadow: '0 12px 30px rgba(0,0,0,0.15)',
                  }}
                  transition={{ type: 'spring', stiffness: 200 }}
                >
                  <a
                    href={repo.html_url}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="project-link"
                  >
                    <h3 className="project-name"> {repo.name}</h3>
                    {repo.description && (
                      <p className="project-description">
                        {repo.description}
                      </p>
                    )}
                  </a>
                </motion.li>
              ))}
            </motion.ul>
          </>
        )}
      </motion.section>

      {/* Mobile-only back to top button */}
      {showScrollTop && (
        <button
          className="back-to-top-btn"
          type="button"
          onClick={scrollToTopOfSection}
        >
           Top
        </button>
      )}
    </div>
  )
}
